.background {
  border-style: solid;
  border-color: black;
  margin-top: 20px;
  /* width: 100%; */
}

.tabsCol {
  border-style: solid;
  border-color: lightgray;
  height: fit-content;
  width: 100%;
}

.form {
  background-color: white;
  border-style: solid;
  border-color: lightgray;
  border-left: none;
  height: fit-content;
  width: 100%;
}

.tab {
  display: flex;
  align-items: center;
  width: 113%;
  height: 60px;
  cursor: pointer;
  font-size: 20px;
  font-weight: 500;
  margin-left: -13px;
  text-transform: none;
  text-align: left;
  background-color: inherit;
  padding-left: 10px;
}

.tab:hover {
  background-color: whitesmoke;
  border-color: #4a148c;
  border-style: solid;
  border-right: none;
}

.tabActive {
  display: flex;
  align-items: center;
  width: 113%;
  height: 60px;
  cursor: pointer;
  font-size: 20px;
  font-weight: 500;
  margin-left: -13px;
  text-transform: none;
  text-align: left;
  background-color: white;
  border-style: solid;
  border-color: #4a148c;
  border-right: none;
}

#dp2 {
  width: 150px;
  height: 150px;
  box-shadow: 0 0 5px 2px gray;
  border-radius: 50%;
  margin: 20px;
}

.dp {
  display: flex;
  align-items: center;
  word-break: break-all;
  font-weight: 500;
  margin-bottom: 10px;
}

.edit {
  border: 1px solid;
  border-radius: 3px;
  padding: 5px;
  margin-left: 48px;
  background-color: #7b1fa2;
  color: white;
}

#upload {
  width: 0;
  margin-bottom: 20px;
}

.botton2 {
  float: right;
  margin-bottom: 15px;
}

@media screen and (max-width: 765px) {
  .tab {
    width: 100%;
    margin: 0 0 0 0;
  }
  .tabActive {
    width: 100%;
    border: solid #4a148c;
  }
  .tab:hover {
    border: solid #4a148c;
  }
}
